@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}

body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #e0e3eb;
}

.container{
  position: relative;
  max-width: 300px;
  width: 100%;
  border-radius: 12px;
  background-color: #fff;
  padding: 10px 20px 20px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
}

.display{
 height: 80px;
 width: 100%;
 outline: none;
 border: none;
 text-align: right;
 font-size: 25px;
 margin-bottom: 10px;
 color: #000e1a;
}


.buttons{
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(4, 1fr);
}

.buttons button{
  padding: 10px;
  border-radius: 5px;
  border: none;
  font-size: 20px;
  cursor: pointer;
  background-color: #eee;
}

.buttons button:active{
  transform: scale(0.99);
}

.operator{
  color: #2f9fff;
}